import { Link } from 'gatsby'
import React from 'react'
import './style.scss'

let randomColor = {};
let titleHover = {};
for (let i = 0; i < 4; i++) {
  randomColor[i] = (function lol(m, s, c) {
    return s[m.floor(m.random() * s.length)] +
        (c && lol(m, s, c - 1));
  })(Math, 'DEF', 4);
  titleHover[i] = {
    backgroundColor: '#' + randomColor[i],
    }
}


const Side = ({ }) => (
  
  <div id="side-toggle" className="side mt-5 menu-default">
      <div className="container-fluid">
        <div className="row">

          <div className="col-lg-3 col-md-12 p-0">
                <div className="col pl-4 title" >
                  <Link to="/about">About Us</Link>
                </div>
                <div className="col pl-4 title" >
                  <Link to="/event">Events</Link>
                </div>
                <div className="col pl-4 title" >
                  <Link to="/faq">FAQ</Link>
                </div>
                <div className="col pl-4 title" >
                  <Link to="/contactus">Contact</Link>
                </div>
          </div>   
        </div>   
      </div>
  </div>
)



export default Side
