import React from 'react'
import { Link } from 'gatsby'
import './style.scss'
import "animate.css/animate.min.css";
import ScrollAnimation from 'react-animate-on-scroll';



class Navi extends React.Component {
/* info toggle
* ------------------------------------------------------ */
  constructor(props) {
    super(props);
    this.state = {isToggleOn: true};

    // This binding is necessary to make `this` work in the callback
    this.ssInfoToggle = this.ssInfoToggle.bind(this);
  }

  ssInfoToggle(){
    document.body.classList.toggle('info-is-visible');
    document.getElementById('side-toggle').classList.toggle('menu-toggle');
    this.setState(state => ({
      isToggleOn: !state.isToggleOn
    }));
  }
  render() {
    const { location, title } = this.props
    return (
      
      <nav className="navbar navbar-expand bg-transparent fixed-top">
        <div className="row">
          <div className="col">
            <Link className="text-center" to="/">
            <p className="navbar-brand m-2">{title}</p>
            </Link> <br></br>
          </div>
          <div className="col">
            <a class="info-toggle" onClick={this.ssInfoToggle} href="#0">
              <span class="info-menu-icon"></span>
            </a>
          </div>
        </div>
      </nav>
    
    )
  }
}

export default Navi
